								</van-grid-item>
							</van-grid>
						</view>
					</view>
				</scroll-view>
			</view>
			<view class="cu-bar bg-white margin-top-xs">
				<view class="action sub-title">
					<text class="text-xl text-bold text-blue text-shadow">我的今日食谱</text>
					<text class="text-ABC text-blue">Daily recipe</text>
				</view>
				<view class="action" @click="goProjectList">
					<text class="text-lg text-grey text-shadow">更多</text>
				</view>
			</view>
			<view class="cu-list menu ">
				<view class="basis-df padding-bottom-xs"></view>
				<view class="cu-item ">
					<navigator class="content" hover-class="none" url="/pages/breakfast/breakfast" open-type="redirect">
						<view class="content justify-between">
							<view>
								<text class="text-black text-xl">早餐</text>
		 				</view>
							<view>
								<text class="text-grey text-xl ">建议378千卡 </text>
								<text class="text-grey cuIcon-roundadd "></text>
							</view>
						</view>
					</navigator>
				</view>
				<view class="basis-df padding-bottom-xs"></view>
				<view class="cu-item ">
					<navigator class="content" hover-class="none" url="/pages/main/breakfast/basicinfo"
						open-type="redirect">
						<view class="content justify-between">
							<view>
								<text class="text-black text-xl">午餐</text>
							</view>
							<view>
								<text class="text-grey text-xl ">建议504千卡 </text>
								<text class="text-grey cuIcon-roundadd "></text>
							</view>
						</view>
					</navigator>
				</view>
				<view class="basis-df padding-bottom-xs"></view>
				<view class="cu-item ">
					<navigator class="content" hover-class="none" url="/pages/myself/basicinfo/basicinfo"
						open-type="redirect">
						<view class="content justify-between">
							<view>
								<text class="text-black text-xl">晚餐</text>
							</view>
							<view>
								<text class="text-grey text-xl ">建议378千卡 </text>
								<text class="text-grey cuIcon-roundadd "></text>
							</view>
						</view>
					</navigator>
				</view>
			</view>

			<!-- 	<view class="cu-card case no-card">

					<view @click="goProject(item.id)" class="cu-item shadow" v-for="(item, index) in projectList"
						:key="index">
						<view class="image">
							<image :src="item.tImg" mode="widthFix"></image>
							<view class="cu-tag bg-gradual-orange">{{item.tabs}}</view>
							<view class="cu-bar bg-shadeBottom"> <text class="text-cut">{{item.type}}</text></view>
						</view>
						<view class="cu-list menu-avatar">
							<view class="cu-item">
								<view class="margin-lr flex-sub">
									<view class="item-name text-grey text-lg">{{item.title}}</view>
									<view class="text-gray text-sm flex justify-between">
										{{item.time}}
										<view class="text-gray text-sm">
											<text class="cuIcon-attentionfill margin-lr-xs"></text>
											{{item.user[0].read}}
											<text class="cuIcon-appreciatefill margin-lr-xs"></text>
											{{item.user[0].like}}
											<text class="cuIcon-shopfill margin-lr-xs"></text> {{item.user[0].use}}
										</view>
									</view>
								</view>
							</view>
						</view>
					</view>
				</view>
		 -->
		</view>



		<view style="height: 140rpx;width: 1rpx;"></view>
	</view>
</template>

<script>
	import request from '@/common/request.js';
	import addTip from "../../components/wxcomponents/struggler-uniapp-add-tip/struggler-uniapp-add-tip.vue"
	export default {
		components: {
			addTip,
		},
		data() {
			return {
				current: {
					userId: 8,
					currentWeight: 155,
					currentHeight: 46,
					currentSugar: 16.2,
					currentEnergy: 311.6,
					currentFat: 16.2
				},
				target: {
					userId: 6,
					targetWeight: 0,
					targetHeight: 0,
					targetSugar: 50,
					targetEnergy: 2000,
					targetFat: 55
				},

				gradientColor: [{
					'0%': '#ffd01e',
					'100%': '#ee0a24',
				}, {
					'0%': '#0081ff',
					'100%': '#1cbbb4',
				}, {
					'0%': '#e03997',
					'100%': '#6739b6',
				}],
				container: null,
				tip: "点击「添加小程序」，下次访问更便捷",
				duration: 1,

				scrollTop: 0,
				old: {
					scrollTop: 0
				},

				bannerList: [{
						imageUrl: 'https://tse1-mm.cn.bing.net/th/id/R-C.5e11fda5b177f51290733a0a5e56599b?rik=gijEoQ%2faNE%2fdzA&riu=http%3a%2f%2fimg.99.com.cn%2fuploads%2f202012%2f449_111438_1.jpg&ehk=7vZcg2qvqq7OS2NbEXdlP5HA%2f6%2bLaHh5840UAARMLLI%3d&risl=&pid=ImgRaw&r=0',
					},
					{
						imageUrl: 'https://tse2-mm.cn.bing.net/th/id/OIP-C.jpmOoKasCkpfRddDBjGaGQHaDP?w=310&h=153&c=7&r=0&o=5&pid=1.7',
					},
					{
						imageUrl: 'https://tse4-mm.cn.bing.net/th/id/OIP-C.SHuggeGyxVoCr06u5ZpwAwHaDf?w=298&h=165&c=7&r=0&o=5&pid=1.7',
					},
					{
						imageUrl: 'https://tse4-mm.cn.bing.net/th/id/OIP-C.RKZRJBeeJtnyUYG7V4e1FAHaDe?w=299&h=164&c=7&r=0&o=5&pid=1.7',
					}
				],
				categories: [{
					cuIcon: 'appreciatefill',
					color: 'red',
					badge: '推荐',
					mid: '1',
					name: '餐谱推荐'
				}, {
					cuIcon: 'selection',
					color: 'orange',
					badge: 1,
					mid: '2',
					name: '体质测试'
				}, {
					cuIcon: 'videofill',
					color: 'yellow',
					badge: 12,
					mid: '3',
					name: '分享视频'
				}, {
					cuIcon: 'group',
					color: 'cyan',
					badge: 22,
					mid: '4',
					name: '分享动态'
				}],
				curriculum: [{
						menu: '香辣牛肉',
						pictureUrl: "http://s1.st.meishij.net/r/97/32/4758097/s4758097_151971999169604.jpg",
					},
					{
						menu: '藜麦鸡肉丸',
						pictureUrl: "http://s1.st.meishij.net/r/178/85/5833928/s5833928_151843048575508.jpg",
					}, {
						menu: '清炒木耳菜',
						pictureUrl: "https://s1.st.meishij.net/r/231/00/2937731/s2937731_152734041810444.jpg",
					}, {
						menu: '香辣牛肉',
						pictureUrl: "http://s1.st.meishij.net/r/97/32/4758097/s4758097_151971999169604.jpg",
					}, {
						menu: '香辣牛肉',
						pictureUrl: "http://s1.st.meishij.net/r/97/32/4758097/s4758097_151971999169604.jpg",
					}



				],
				projectList: [],
			}
		},
		watch: {

		},
		mounted() {
			console.log(this.projectList)
			this.getData();
			this.$nextTick(function() {
				console.log(uni.createSelectorQuery().in(this))
				const query = uni.createSelectorQuery().in(this)
				query.select('#container').boundingClientRect(data => {
					console.log(data)
					this.container = data
				}).exec();
			})
		},
		methods: {
			getData() {
				let user = {
					user_id: '8'
				}
				console.log('数据加载')

				// 获取目标身体状态
				let optstar = {
					url: 'user/getUserTarget',
					method: 'get',
				};
				uni.showLoading({
					title: '加载中'
				})
				request.httpRequest(optstar, user).then(res => {
					console.log(res);
					uni.hideLoading();
					if (res.statusCode == 200) {
						this.target = res.data;
						console.log(this.target)
					} else {}
				});

				// 获取当前身体状态
				let optscur = {
					url: 'user/getUserCurrent',
					method: 'get',
				};
				request.httpRequest(optscur, user).then(res => {
					console.log(res);
					uni.hideLoading();
					if (res.statusCode == 200) {
						this.current = res.data;
						console.log(this.current)
					} else {}
				});

				// 获取早餐推荐食谱
				let optsbreak = {
					url: 'recommend/recommendByUserbreakfast',
					method: 'get',
				};
				request.httpRequest(optsbreak, user).then(res => {
					console.log(res);
					uni.hideLoading();
					if (res.statusCode == 200) {
						console.log(this.res.data)
					} else {}
				});
			},
			scroll: function(e) {
				console.log(e)
				this.old.scrollTop = e.detail.scrollTop
			},
			goCategorieslist: function(e) {
				// console.log(e.currentTarget.dataset.mid)
				if (e.currentTarget.dataset.mid == 2) {
					uni.navigateTo({
						url: '../me/mentalTest/index?mid=1'
					})
				}
				// if (e.currentTarget.dataset.mid == 1 || e.currentTarget.dataset.mid == 2) {
				// 	uni.navigateTo({
				// 		url: '../timeline?mid=' + e.currentTarget.dataset.mid
				// 	})
				// } else if (e.currentTarget.dataset.mid == 3) {
				// 	uni.navigateTo({
				// 		url: '../project/list'
				// 	})
				// }
			},
			goProjectList() {
				uni.navigateTo({
					url: '../project/list'
				})
			},
			goProject(id) {
				uni.navigateTo({
					url: '../project/project?proId=' + id
				})
			},
			goVideo() {
				uni.navigateTo({
					url: '../video'
				})
			}
		}
	}
</script>
<style lang="scss" scoped>
	@font-face {
		font-family: 'iconfont';
		src: url('@/static/iconfont.ttf?t=1647423422630') format('truetype');
	}

	.iconfont {
		font-family: "iconfont" !important;
		font-size: 16px;
		font-style: normal;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}

	.content {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

