	}

	.gridtext1 {
		float: left;
		color: #888;
		font-size: 26rpx;
		line-height: 26rpx;
	}

	.custom-class {
		border: 0px;
		border-radius: 10%;
	}

	.van-grid-item__content:after {
		border-width: 0 0px 0px 0 !important;
		z-index: 1;
		border-top-width: 0px;
		border-right-width: 0px !important;
		border-bottom-width: 0px !important;
